import { Project } from '../../App';
import React from 'react';
import { View, useWindowDimensions } from 'react-native';
import { CustomPressable } from './buttons/CustomPressable';
import { usePopup } from '../navigation/popup/PopupProvider';
import ProjectPopup from './ProjectPopup';
import AppIcon from './AppIcon';
import Text from './Text';
import { Theme } from '../hooks/useTheme';

export default function ProjectCard({ theme, p }: { theme: Theme; p: Project }) {
  const { width } = useWindowDimensions();
  const { openPopup } = usePopup();
  return (
    <CustomPressable
      onPress={() => {
        openPopup({
          component: <ProjectPopup title={p.title} text={p.text} closeOnCTA project={p} />,
        });
      }}
      style={{
        flex: width < 800 ? undefined : width < 1200 ? '33%' : '25%',
      }}
    >
      <View
        style={{
          backgroundColor: theme.colors.projectCardBackground,
          padding: theme.space.l,
          //minWidth: width < 800 ? undefined : 300,
          //maxWidth: width < 800 ? undefined : '300',
          borderRadius: 4,
          minHeight: '100%',
        }}
      >
        <View style={{ flexDirection: 'row', alignItems: 'center' }}>
          {p.logoComp && (
            <View style={{ marginBottom: theme.space.s, marginRight: theme.space.l }}>{p.logoComp}</View>
          )}
          {p.appIconSources && (
            <AppIcon
              appIconSource={p.appIconSources[0]}
              style={{ marginRight: theme.space.l, alignSelf: 'flex-start' }}
            />
          )}
          <View style={{ flex: 1, gap: theme.space.m, alignItems: 'flex-end', alignSelf: 'flex-start' }}>
            {p.period && (
              <View
                style={{
                  paddingHorizontal: theme.space.s,
                  borderRadius: 4,
                  borderWidth: 1,
                  borderColor: theme.colors.text,
                  width: 'fit-content',
                  marginBottom: -theme.space.s,
                }}
              >
                <Text
                  style={{
                    color: theme.colors.text,
                    fontSize: 12,
                  }}
                >
                  {p.period}
                </Text>
              </View>
            )}
            <Text
              style={{
                fontSize: 20,
                fontWeight: 'bold',
                textAlign: 'right',
                color: theme.colors.text,
              }}
            >
              {p.title}
            </Text>
            <Text
              style={{
                fontStyle: 'italic',
                textAlign: 'right',
                marginBottom: p.text ? 0 : theme.space.m,
                color: theme.colors.text,
              }}
            >
              {p.subtitle}
            </Text>
          </View>
        </View>
        {p.text && (
          <View style={{ marginTop: theme.space.m }}>
            <Text
              style={{
                textAlign: 'right',
                color: theme.colors.text,
              }}
            >
              {p.text.length > 180 ? p.text.slice(0, 180) + '...' : p.text}
            </Text>
          </View>
        )}
        {p.status && (
          <View
            style={{
              paddingHorizontal: theme.space.s,
              borderRadius: 4,
              borderWidth: 1,
              borderColor: p.status === 'live' ? '#228B22' : theme.colors.text,
              width: 'fit-content',
              //marginBottom: -theme.space.s,
              alignSelf: 'flex-end',
            }}
          >
            <Text
              style={{
                color: p.status === 'live' ? '#228B22' : theme.colors.text,
                fontSize: 12,
              }}
            >
              {p.status}
            </Text>
          </View>
        )}
      </View>
    </CustomPressable>
  );
}
