import { Box, Canvas, rect, rrect } from '@shopify/react-native-skia';
import React from 'react';
import { TextProps, View, useWindowDimensions } from 'react-native';
import useTheme from '../hooks/useTheme';

export default function PhoneFrame({ children, ...props }: TextProps) {
  const { height: screenHeight, width: screenWidth } = useWindowDimensions();

  const theme = useTheme();
  const width = Math.min(screenWidth * 0.5, 400);
  const height = width * 2;

  const radius = width * 0.14;
  const innerRadius = radius * 0.65;
  const innerStrokeWidth = width * 0.03;
  const outerStrokeWidth = width * 0.02;

  const space = theme.space.m;

  const innerWidth = width - space * 2 - innerStrokeWidth * 2 - outerStrokeWidth;
  const innerHeight = height - space * 2 - innerStrokeWidth * 2 - outerStrokeWidth;
  const innerLeft = space + innerStrokeWidth + outerStrokeWidth * 0.5;
  const innerTop = space + innerStrokeWidth + outerStrokeWidth * 0.5;

  return (
    <View style={{ width, height }}>
      <Canvas style={{ flex: 1 }}>
        <Box
          box={rrect(rect(space, space, width - space * 2, height - space * 2), radius, radius)}
          color="#666"
          style="stroke"
          strokeWidth={outerStrokeWidth}
        ></Box>
        <Box
          box={rrect(
            rect(
              space + outerStrokeWidth + 2,
              space + outerStrokeWidth + 2,
              innerWidth + innerStrokeWidth,
              innerHeight + innerStrokeWidth
            ),
            radius * 0.8,
            radius * 0.8
          )}
          color="black"
          style="stroke"
          strokeWidth={innerStrokeWidth}
        ></Box>
      </Canvas>
      <View
        style={{
          position: 'absolute',
          width: innerWidth,
          height: innerHeight,
          left: innerLeft,
          top: innerTop,
          borderRadius: innerRadius,
          overflow: 'hidden',
        }}
      >
        {children}
      </View>
    </View>
  );
}
