import React from 'react';
import { View, Image, ImageSourcePropType, ViewStyle } from 'react-native';

const SIZE = 80;

export default function AppIcon({
  appIconSource,
  style,
}: {
  appIconSource: ImageSourcePropType;
  style?: ViewStyle;
}) {
  return (
    <View
      style={[
        {
          width: SIZE,
          height: SIZE,
          shadowRadius: 12,
          shadowOpacity: 0.25,
          shadowColor: 'black',
          shadowOffset: {
            width: 4,
            height: 6,
          },
          borderRadius: SIZE * 0.2,
          overflow: 'hidden',
        },
        style,
      ]}
    >
      <Image
        source={appIconSource}
        style={{
          flex: 1,
        }}
        resizeMode="cover"
      />
    </View>
  );
}
