import React from 'react';
import { TouchableOpacityProps, ViewProps, Linking } from 'react-native';
import Icon from 'react-native-vector-icons/MaterialCommunityIcons';
import useTheme from '../../hooks/useTheme';
import Text from '../Text';
import { CustomPressable } from './CustomPressable';

interface Props extends ViewProps, TouchableOpacityProps {
  icon?: string;
  text: string;
  link?: string;
  onPress?: () => void;
  inverted?: boolean;
}

export default function Button({ icon, text, link, style, inverted, onPress }: Props) {
  const theme = useTheme();

  return (
    <CustomPressable
      style={[
        {
          flexDirection: 'row',
          alignItems: 'center',
          paddingHorizontal: theme.space.l,
          paddingVertical: theme.space.s,
          borderRadius: 4,
          borderWidth: 1,
          borderColor: '#666',
          width: 'fit-content',
          gap: theme.space.s,
        },
        style,
      ]}
      onPress={() => {
        !!link && Linking.openURL(link);
        !!onPress && onPress();
      }}
    >
      {icon && <Icon name={icon} size={20} color={inverted ? 'black' : 'white'} />}
      {text && (
        <Text
          style={{
            color: inverted ? 'black' : 'white',
          }}
        >
          {text}
        </Text>
      )}
    </CustomPressable>
  );
}
