import { useWindowDimensions } from 'react-native';

export enum DEVICE_SIZES {
  xs = 400, // iPhone SE = 375
  s = 450, // phone XR = 414
  m = 820, // tablet = 768
  l = 1000, // desktop = 1024
  xl = 1400, // large desktop = 1920
}

export default function useDeviceSize() {
  // sizes are based on device width
  const { width: screenWidth } = useWindowDimensions();

  if (screenWidth > DEVICE_SIZES.xl) return DEVICE_SIZES.xl;
  if (screenWidth > DEVICE_SIZES.l) return DEVICE_SIZES.l;
  if (screenWidth > DEVICE_SIZES.m) return DEVICE_SIZES.m;
  if (screenWidth > DEVICE_SIZES.s) return DEVICE_SIZES.s;
  return DEVICE_SIZES.xs;
}
